import { motion, useAnimation } from "framer-motion";
import React, { useEffect, useState } from "react";
import { createUseStyles } from "react-jss";

interface Props {}

const useStyles = createUseStyles({
  p: {
    fontWeight: 600,
    fontSize: 19,
    letterSpacing: "0.02em",
    transform: "translate(-50%, -50%)",
    left: "50%",
    top: "36%",
    position: "absolute",
    width: "36%",
  },
});

const variants = {
  enter: { opacity: 1, transition: { duration: 2 } },
  exit: { opacity: 0, transition: { duration: 0.5 } },
};

export const About = (props: Props) => {
  const classes = useStyles();
  return (
    <motion.div initial={"exit"} animate={"enter"} exit={"exit"}>
      <motion.p className={classes.p} variants={variants}>
        I'm a mobile developer who is pasionate about details and loves to be
        organized, currently working in side projects on my free time but
        available for part time/freelance projects.
      </motion.p>
    </motion.div>
  );
};
