import { motion, useAnimation } from "framer-motion";
import React, { useEffect, useState } from "react";
import { createUseStyles } from "react-jss";
import { useLocation } from "react-router";

interface Props {}

const useStyles = createUseStyles({
  list: {
    fontWeight: "700",
    fontStyle: "italic",
    fontSize: 36,
    color: "var(--primary)",
    letterSpacing: "0.1em",
    position: "absolute",
    top: "20%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    listStyleType: "none",
    display: "inline-flex",
    margin: 0,
    padding: 0,
  },
});

export const RouteName = (props: Props) => {
  const location = useLocation();
  const classes = useStyles();
  const [route, setRoute] = useState("Projects Page");
  const controls = useAnimation();

  useEffect(() => {
    if (location.pathname === "/" && route !== "Projects Page") {
      controls.start("hidden").then(() => setRoute("Projects Page"));
    }
    if (location.pathname === "/about" && route !== "About") {
      controls.start("hidden").then(() => setRoute("About"));
    }
    if (location.pathname === "/contact" && route !== "Contact") {
      controls.start("hidden").then(() => setRoute("Contact"));
    }
  }, [location]);

  useEffect(() => {
    controls.start("visible");
  }, [route]);

  const variants = {
    visible: (i: number) => ({
      opacity: 1,
      y: 20,
      transition: {
        duration: i * 0.1,
      },
    }),
    hidden: (i: number) => ({
      opacity: 0,
      y: 0,
      transition: {
        duration: i * 0.1,
      },
    }),
  };

  return (
    <ul className={classes.list}>
      {route.split("").map((char, i) => (
        <motion.li custom={i} animate={controls} variants={variants}>
          {char}
        </motion.li>
      ))}
    </ul>
  );
};
