import React from "react";
import { Link } from "react-router-dom";

interface Props {
  name: string;
  link: string;
  position: "left" | "right";
}

export const TopLink = (props: Props) => {
  return (
    <Link
      to={props.link}
      style={{
        position: "absolute",
        top: "4%",
        [props.position]: "4%",
        color: "var(--text)",
        textDecoration: "none",
        fontWeight: 500,
        fontSize: 20,
      }}
    >
      {props.name}
    </Link>
  );
};
