import React, { useEffect } from "react";
import { createUseStyles } from "react-jss";
import { Link } from "react-router-dom";
import { contactLinks } from "../constants/contactlinks";
import { ContactButtons, RouteName, TopLink } from "./components";
import { Routes } from "./navigation";

interface Props {}

const useStyles = createUseStyles({
  div: {
    backgroundColor: "var(--background)",
    width: "100vw",
    height: "100vh",
    display: "flex",
  },
  title: {
    fontWeight: 700,
    fontSize: 32,
    color: "var(--text)",
    top: "4%",
  },
  subtitle: {
    fontWeight: 700,
    fontSize: 22,
    color: "var(--text)",
    top: "calc(4% + 54px)",
  },
  center: {
    transform: "translate(-50%, -50%)",
    left: "50%",
    position: "absolute",
  },
});

export const Main = (props: Props) => {
  const classes = useStyles();
  useEffect(() => {
    console.log("renderrr");
  }, []);
  return (
    <div className={classes.div}>
      <Link to={"/"} style={{ textDecoration: "none", color: "inherit" }}>
        <p className={`${classes.center} ${classes.title}`}>Tomas Tamagnone</p>
        <p className={`${classes.center} ${classes.subtitle}`}>
          Mobile Developer
        </p>
      </Link>
      <ContactButtons links={contactLinks} />
      <TopLink name={"about."} link={"/about"} position={"left"} />
      <TopLink name={"contact."} link={"/contact"} position={"right"} />
      <RouteName />
      <Routes />
    </div>
  );
};
