import { motion } from "framer-motion";
import React from "react";
import { createUseStyles } from "react-jss";
import { contactLinks } from "../constants";

interface Props {}

const useStyles = createUseStyles({
  div: {
    display: "flex",
    flexDirection: "column",
    top: "50%",
    left: "50%",
    position: "absolute",
    transform: "translate(-50%, -50%)",
  },
  links: {
    display: "flex",
    justifyContent: "space-between",
    width: "100%",
    margin: 0,
  },
  link: {
    textDecoration: "none",
    color: "var(--text)",
    fontWeight: 500,
    fontSize: 32,
    marginBottom: 24,
    marginTop: 12,
  },
  text: {
    color: "var(--text)",
    fontWeight: 400,
    fontSize: 22,
    margin: 0,
  },
  title: {
    color: "var(--text)",
    fontWeight: 600,
    fontSize: 30,
  },
});

const variants = {
  enter: { opacity: 1, transition: { duration: 2 } },
  exit: { opacity: 0, transition: { duration: 0.5 } },
};

export const Contact = (props: Props) => {
  const classes = useStyles();
  return (
    <motion.div
      className={classes.div}
      initial={"exit"}
      animate={"enter"}
      exit={"exit"}
    >
      <motion.p className={classes.title} variants={variants}>
        if you want to talk, have a project or just say hi
      </motion.p>
      <motion.p className={classes.text} variants={variants}>
        email:
      </motion.p>
      <motion.a
        className={classes.link}
        href="mailto:tomastamagnone.lamarti@gmail.com"
        variants={variants}
      >
        tomastamagnone.lamarti@gmail.com
      </motion.a>
      <motion.p className={classes.text} variants={variants}>
        or:
      </motion.p>
      <motion.p className={classes.links} variants={variants}>
        {contactLinks.map((link) => (
          <motion.a
            className={classes.link}
            href={link.link}
            variants={variants}
          >
            {link.name}
          </motion.a>
        ))}
      </motion.p>
    </motion.div>
  );
};
